<template>
  <form action="#" @submit.prevent="submit">
    <h2>Основная информация</h2>
    <div class="form-item">
      <label for="last-name" class="form-item__label">Фамилия &#8432; :</label>
      <input type="text" name="last-name" class="form-item__input-text" :class="{ 'form-item__input_error' : $v.lastName.$error}" v-model.trim="$v.lastName.$model">
      <p class='error-msg' v-if='$v.lastName.$error'> &#8432; Обязательно для заполнения</p>
    </div>
    <div class="form-item">
      <label for="first-name" class="form-item__label">Имя &#8432; :</label>
      <input type="text" name="first-name" class="form-item__input-text" :class="{ 'form-item__input_error' : $v.firstName.$error}" v-model.trim="$v.firstName.$model">
      <p class='error-msg' v-if='$v.firstName.$error'> &#8432; Обязательно для заполнения</p>
    </div>
    <div class="form-item">
      <label for="middle-name" class="form-item__label">Отчество:</label>
      <input type="text" name="middle-name" class="form-item__input-text">
    </div>
    <div class="form-item">
      <label for="birth-date" class="form-item__label">Дата рождения &#8432; :</label>
      <input type="text" name="birth-date" class="form-item__input-text" :class="{ 'form-item__input_error' : $v.birthDate.$error}" v-model.trim="$v.birthDate.$model" placeholder="дд.мм.гггг">
      <p class='error-msg' v-if='$v.birthDate.$error'> &#8432; Обязательно для заполнения</p>
    </div>
    <div class="form-item">
      <label class="form-item__label">Номер телефона &#8432; :</label>
      <input type="text" class="form-item__input-text" :class="{ 'form-item__input_error' : !$v.phoneNumber.required || !$v.phoneNumber.customPhone }" v-model.trim="$v.phoneNumber.$model">
      <p class='error-msg' v-if='!$v.phoneNumber.required'> &#8432; Обязательно для заполнения</p>
      <p class='error-msg' v-if='!$v.phoneNumber.customPhone'> &#8432; Неверный номер (формат: +7xxxxxxxxxx)</p>
    </div>
    <div class="form-item form-item__gender">
      <label class="form-item__label">Пол:</label>
      <input type="radio" value="male" class="form-item__genderMale">
      <input type="radio" value="female" class="form-item__genderFemale">
    </div>
    <div class="form-item">
     <div class="form-item__multibox">
      <label for="clients-group" class="form-item__label">Группа клиентов &#8432; :</label>
      <select name="" class="form-item__select form-item__multiselect" multiple :class="{ 'form-item__input_error' : $v.clientsGroup.$error}" v-model.trim="$v.clientsGroup.$model">
          <option v-for="client in clients" :key="client" :value="client">{{ client }} </option>
      </select>
     </div>
      <p class='error-msg' v-if='$v.clientsGroup.$error'> &#8432; Необходимо выбрать группу </p>   
    </div>
    <div class="form-item">
      <label for="{doctor}" class="form-item__label">Лечащий врач:</label>
      <select name="doctor" class="form-item__select">
        <option value="{doctor}" v-for="doctor in doctors" :key="doctor"> {{ doctor }}</option>
      </select>
    </div>
    <div class="form-item">
      <input type="checkbox" name="sms-check" id="sms-check"><label for="sms-check" class="form-item__label form-item__label_checker">Не отправлять СМС</label>
    </div>
    
    <h2>Адрес</h2>
    <div class="block__section">
      <div class="form-item block__index">
        <label class="form-item__label">Индекс:</label>
        <input type="text" name="adress-index" class="form-item__input-text">
      </div>
      <div class="form-item block__country">
        <label class="form-item__label">Страна:</label>
        <input type="text" name="adress-index" class="form-item__input-text">
      </div>
    </div>
    <div class="form-item">
      <label class="form-item__label">Область:</label>
      <input type="text" name="adress-county" class="form-item__input-text">
    </div>
    <div class="form-item">
      <label class="form-item__label">Город  &#8432; :</label>
      <input type="text" name="adress-city" class="form-item__input-text" :class="{ 'form-item__input_error' : $v.city.$error}" v-model.trim="$v.city.$model">
      <p class='error-msg' v-if='$v.city.$error'> &#8432; Обязательно для заполнения</p>
    </div>
    <div class="block__section">
      <div class="form-item block__street">
        <label class="form-item__label">Улица:</label>
        <input type="text" name="adress-street" class="form-item__input-text">
      </div>
      <div class="form-item block__house">
        <label class="form-item__label">Дом:</label>
        <input type="text" name="adress-house" class="form-item__input-text">
      </div>
    </div>
    
    <h2>Паспорт</h2>
    <div class="form-item">
      <label class="form-item__label">Тип документа  &#8432; :</label>
      <select name="document-type" class="form-item__select" :class="{ 'form-item__input_error' : $v.documentType.$error}" v-model.trim="$v.documentType.$model">
        <option value="{document}" v-for="document in documents" :key="document"> {{ document }}</option>
      </select>
      <p class='error-msg' v-if='$v.documentType.$error'> &#8432; Выберите тип документа</p>
    </div>
    <div class="block__section">
      <div class="form-item block__ser">
        <label class="form-item__label">Серия:</label>
        <input type="text" name="document-ser" class="form-item__input-text">
      </div>
      <div class="form-item block__number">
        <label class="form-item__label">Номер:</label>
        <input type="text" name="document-number" class="form-item__input-text">
      </div>
    </div>
    <div class="block__section">
      <div class="form-item block__who">
        <label class="form-item__label">Кем выдан:</label>
        <input type="text" name="document-who" class="form-item__input-text">
      </div>
      <div class="form-item block__when">
        <label class="form-item__label">Дата выдачи:</label>
        <input type="text" name="document-when" class="form-item__input-text">
      </div>
    </div>
    <p class='submit__error' v-if="submitStatus === 'error'">Пожалуйста, заполните все обязательные поля</p><p class='submit__success' v-if="submitStatus === 'ok'">Пользователь зарегистрирован!</p>
    <input type="submit" value="ОТПРАВИТЬ">
  </form>
</template>


<script>
import { required, helpers } from 'vuelidate/lib/validators'
  
//build with regex101.com
const customPhone = helpers.regex('customePhone', /\+7\d{10}/g)
  
export default {
  
  data() {
      return {
          clients: ['VIP', 'Проблемные', 'ОМС'],
          doctors: ['Иванов', 'Захаров', 'Чернышева'],
          documents: ['Паспорт', 'Свидетельство о рождении', 'Вод. удостоверение'],
          lastName: '',
          firstName: '',
          birthDate: '',
          phoneNumber: '+7',
          clientsGroup: [],
          city: '',
          documentType: [],
          submitStatus: ''          
      }
  },
  
  validations: {
    lastName: {
      required
    },
    firstName: {
      required,
    },
    birthDate: {
      required
    },
    phoneNumber: {
      required,
      customPhone
    },
    clientsGroup: {
      required
    },
    city: {
      required
    },
    documentType: {
      required
    }
    
  },
  
  methods: {
      submit() { 
        console.log('HAHE');
        this.$v.$touch();
        if(this.$v.$invalid) {
          this.submitStatus = 'error'
        } else {
          this.submitStatus = 'ok'
        }
      },
  },
}
  
</script>


<style lang="scss" scoped>
    
  h2 {
    margin-top: 1.4rem;
  }
  
  .form-item {
      display: flex;
      position: relative;
      flex-direction: column;
      margin: .5rem;

    &__label {
      display: flex;
      align-items: center;
    }
    
    &__input-text,
    &__select {
      margin-top: .4rem;
      border-radius: 5px;
      border: 1px solid darken($bg, 10%);
      padding: .3rem;
    }
    
    &__multibox {
      display: flex;
      flex-direction: row;
      
      option:target {
        background-color: purple;
      }
    }
    
    &__multiselect:enabled {
      display: flex;
      width: 50%;
      margin-left: 1rem;
      max-height: 4rem;
      overflow: auto;
      border: 2px solid darken($bg, 10%);
      background-color: darken($bg, 5%);
      color: $text;
    }
    
    &__input-text:focus,
    &__select:focus {
      outline-style: none;
      box-shadow: 0 5px 10px rgba(darken($bg, 20%), .5)
    }
    
    &__gender {
      display: flex;
      flex-direction: row;
      align-items: center;
      
      label {
        margin-right: .5rem;
      }
    }
    
    &__genderMale,
    &__genderFemale {
      -webkit-appearance: none;
      background-color: white;
      cursor: pointer;
      width: 40px;
      height: 20px;
      margin: 0 -1.5px;
    }
    
    &__genderMale:after {
      content: 'M';
      display: flex;
      justify-content: center;
      align-items: center;
      border: 2px solid darken($bg, 20%);
    }
    
    &__genderFemale:after {
      content: 'Ж';
      display: flex;
      justify-content: center;
      align-items: center;
      border: 2px solid darken($bg, 20%);
    }
    
    &__genderMale:focus,
    &__genderFemale:focus {
      outline-style: none;
      background-color: darken($bg, 5%);
      color: $text;
    }
    
    &__label_checker {
      cursor: pointer;
    }
    
    input[type="checkbox"] {
        -webkit-appearance: none;
        cursor: pointer;
    }
    
    &__label_checker::before {
      content: '';
      position: absolute;
      left: 146px;
      width: 20px;
      height: 20px;
      background-color: lighten($bg, 30%);
      -webkit-appearance: none;
      border: 2px solid darken($bg, 20%);
      border-radius: 5px;
      margin-left: .5em;
      outline: none;
      z-index: 0;
    }
    
    input[type='checkbox']:checked ~ &__label_checker::after{
      content: '';
      width: 10px;
      height: 10px;
      border-radius: 5px;
      background-color: darken($bg, 20%);
      z-index: 1;
      margin-left: .72rem;
      transition: .4s;
    } 
  }
  
  .block {
    
    &__section {
      display: flex;
      flex-direction: row;
    }
    
    &__index,
    &__house,
    &__ser,
    &__when {
      width: 30%;
    }
    
    &__country,
    &__street,
    &__number,
    &__who {
      width: 70%;
    }
    
    &__who {
      padding-bottom: 3rem;
    }
}
  
  input[type="submit"] {
    display: flex;
    justify-content: center;
    margin: 1rem auto 3rem;
    width: 50%;
    padding: .5rem;
    border-radius: 5px;
    border: 1px solid darken($bg, 10%);
    background-color: darken($bg, 5%);
    color: $text;
    cursor: pointer;
    letter-spacing: .1rem;
    transition: .1s;
    outline: none;
    box-shadow: 0px 10px 10px rgba(lighten($bg, 20%), .2),
                  0px 0px 15px rgba(lighten($bg, 20%), .4);
    
    &:hover,
    &:active {
      color: darken($bg, 20%);
      background-color: lighten($bg, 25%);
      width: 40%;
      transition: .2s;
      outline: none;
    }
  }
  
  .form-item__input_error {
    border: 2px solid lighten(#b80f16, 20%);
  }
  
  .error-msg {
    display: flex;
    background-color: lighten($bg, 20%);
    padding: .3rem;
    margin-right: auto;
    border-radius: 5px;
    color: lighten(#b80f16, 10%);
    margin-top: .5rem;
  }
  
  .submit {
    
    &__error {
      @extend .error-msg;
      display: flex;
      justify-content: center;
      text-align: center;
      width: 80%;
      margin: auto;
    }
    
    &__success {
      @extend .error-msg;
      @extend .submit__error;
      color: green;
    }
  }
  
  @media (max-width: 650px) {
    .block {  
      
      &__index,
      &__house,
      &__ser,
      &__when {
        width: 50%;
      }

      &__country,
      &__street,
      &__number,
      &__who {
        width: 50%;
      }
    }
  }
</style>